.hexagon {
  position: relative;
  width: 60px; 
  height: 34.64px;
  background-color: #221e1e;
  margin: 17.32px 0;
  border-left: solid 5px #ffffff;
  border-right: solid 5px #20cff6;
  -webkit-transform: rotate(30deg);
      -ms-transform: rotate(30deg);
          transform: rotate(30deg);
  -webkit-box-shadow: 0 0 20px rgb(32, 203, 242);
          box-shadow: 0 0 20px rgb(32, 203, 242);
}

.hexagon:before,
.hexagon:after {
  content: "";
  position: absolute;
  z-index: 1;
  width: 42.43px;
  height: 42.43px;
  -webkit-transform: scaleY(0.5774) rotate(-45deg);
  -ms-transform: scaleY(0.5774) rotate(-45deg);
  transform: scaleY(0.5774) rotate(-45deg);
  background-color: inherit;
  left: 3.7868px;
  -webkit-box-shadow:0 0 20px rgb(32, 203, 242);
          box-shadow:0 0 20px rgb(32, 203, 242);
}

.hexagon:before {
  top: -21.2132px;
  border-top: solid 7.0711px #12b3d7;
  border-right: solid 7.0711px #20cff6;
}

.hexagon:after {
  bottom: -21.2132px;
  border-bottom: solid 7.0711px #15abcd;
  border-left: solid 7.0711px #ffffff;
}
.hexagon span {
  display: block;
  position: absolute;
  top:2.8867513459481287px;
  left: 0;
  width:50px;
  height:28.8675px;
  z-index: 2;
  background: inherit;
}



